///  main: _module.scss
///  ==========================================================================
///  Base styles section
///  ==========================================================================

// Border-box everywhere!
* {
  box-sizing: border-box;
  font-size: 100%;
  &::before, &::after {
    box-sizing: border-box;
  }
}

// Really trying to target everything here
*, html, body, button, input, select, textarea, ::-webkit-input-placeholder {
  font-family: var(--font)!important;
}
